body {
  color: #212121;
  background-color: rgba(0, 255, 255, .494);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

ul {
  list-style: none;
}

ul, li {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  background-color: var(--container-color);
  justify-content: center;
  margin: 0 auto;
  padding: 10px 15px;
  display: flex;
  box-shadow: 1px 2px 3px #212121;
}

.input {
  letter-spacing: .02em;
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-right: 20px;
  padding: 5px 10px;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  box-shadow: 1px 2px 3px #212121;
}

.btn {
  cursor: pointer;
  color: #212121;
  background-color: #0ff;
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 18px;
  transition: background-color .25s ease-in-out;
  box-shadow: 1px 2px 3px #212121;
}

.btn:hover, .btn:focus {
  color: #212121;
  background-color: #00c3ff;
}

